
import XButton from 'vux/src/components/x-button/index.vue'
import AlertModule from 'vux/src/plugins/alert/module'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import md5 from 'vux/src/tools/md5/index.js'


export default {
components: {
XButton,
Group,
XInput
},
name: 'login',
data () {
return {
propUser: this.user,
loginText: '??????/Login',
loginLoading: false
}
},
created () {


},
methods: {
loginClick () {
const self = this
if (self.propUser.userName === '' || self.propUser.password === '') {
AlertModule.show({
title: '??????/Warning',
content: '??????????????????????????????<br />Check the username and password',
buttonText: '??????/Back'
})
} else {
self.loginLoading = true
self.loginText = 'Processing'

self.propUser.password = md5(self.propUser.password)
self.$http.get('http://localhost/index.php/basedata/assist?action=list&typeNumber=PayMethod&isDelete=2').then(res => {
console.log(JSON.stringify(res))
})




























}
}
},
props: ['config', 'user']
}



